const Button = ({ children, variant = 'primary', className = '', ...props }: any) => {
    const baseStyle = "inline-flex items-center justify-center px-6 py-3 text-sm font-semibold transition-all duration-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed";

    const variants = {
        primary: "bg-teal-600 text-white hover:bg-teal-700 hover:shadow-lg hover:shadow-teal-600/20 focus:ring-teal-500 border border-transparent",
        secondary: "bg-white text-slate-700 hover:bg-slate-50 hover:text-teal-600 border border-slate-200 shadow-sm",
        outline: "bg-transparent text-slate-600 hover:text-teal-600 border border-slate-200 hover:border-teal-200"
    };

    return (
        <button className={`${baseStyle} ${variants[variant as keyof typeof variants]} ${className}`} {...props}>
            {children}
        </button>
    );
};

export default Button;